.mainDiv {
    align-items: center;
    background-color: #d8e3e7;
    margin: 0px;
    height: 2200px;
}

.state_name {
    margin-top: 20px;
    width: 500px;
    height: 50px;
    font-size: 20px;
    border-radius: 5px;
    border-width: 0px;
    padding-left: 10px;
}

.autoContainer {
    background-color: white;
    width: 500px;
    align-items: start;
    border-width: 10px;
    border-color: #282c34;
    border-radius: 5px;
    position: absolute;
    margin-top: 70px;
}

.option {
    margin-top: 5px;
    margin-bottom: 5px;
    cursor: pointer;
}

.option:hover {
    background-color: cadetblue;
}

.option-selected {
    background-color: cadetblue;
}

.searchButton {
    color: white;
    display: inline;
    border-width: 0px;
    border-radius: 30px;
    margin-top: 20px;
    width: 300px;
}

