.navbar-header {
    display: flex;
    flex-direction: row;
    align-items: center;
    background-color: #005a8d;
    height: 50px;
}

.covid-heading {
    margin-left: 20px;
    color: white;
}

.buttton-heading {
    color: white;
    height: 50px;
    border-width: 0;
}

.overall-margin {
    margin-left: 75%;
}

@media (max-width: 1700px) {
    .overall-margin {
        margin-left: 65%;
    }
}

@media (max-width: 1400px) {
    .overall-margin {
        margin-left: 60%;
    }
}

@media (max-width: 1200px) {
    .overall-margin {
        margin-left: 55%;
    }
}

@media (max-width: 1100px) {
    .overall-margin {
        margin-left: 50%;
    }
}

@media (max-width: 1000px) {
    .overall-margin {
        margin-left: 45%;
    }
}
@media (max-width: 800px) {
    .overall-margin {
        margin-left: 30%;
    }
}

@media (max-width: 700px) {
    .overall-margin {
        margin-left: 25%;
    }
}

@media (max-width: 570px) {
    .overall-margin {
        margin-left: 20%;
    }

    h1 {
        font-size: 20px;
    }

    h2 {
        font-size: 15px;
    }
}
